<template>
  <div id="app">

    <div class="container">
      <div class="row">
        <p class="lng col-md-4 text-left">총 <span>{{ 카카오들.length }}</span>개의 상품이 조회되었습니다.</p>

        <div class="float-right col-md-8">
          <b-form-select v-model="selected" class="mb-3 sel float-right" v-on:change="priceSort()">
            <b-form-select-option value="1">판매량 순</b-form-select-option>
            <b-form-select-option value="2">낮은 가격순</b-form-select-option>
            <b-form-select-option value="3">높은 가격순</b-form-select-option>
         </b-form-select>
         <!-- // 버튼 메써드 확인 해봄
         <div>{{ selected }}</div>
         <div class="float-right">
          <button v-on:click="priceSortA">판매량순</button>
          <button v-on:click="priceSortB">낮은가격</button>
          <button v-on:click="priceSortC">높은가격</button>
         </div> -->

          <div id="btn-menu-vue" class="btn-menu" @click="onActive">클래스추가하기</div>
          <div class="menu vue" :class="{ on: isActive }"></div>

         </div>
      </div>
      <div class="row" v->
          <!-- <router-view v-for="(자료,i) in 카카오들" v-bind:카카오들="자료" :key="i" /> -->
          <Card v-for="(자료,i) in 카카오들" v-bind:카카오들="자료" :key="i"></Card> 
     </div>
    </div>
  </div>
</template>

<script>
import Card from './components/Card.vue'
import data from './assets/data.js'
export default {
  name: 'App',
  data(){
    return{
      카카오들 : data,
      카카오들오리지널 : [...data], // priceSortA의 기능을 위해 원본 데이터 카피
      selected: '1',
        isActive: false,
    flag: false
     }
  },
  methods: {
    priceSortA(){
        this.카카오들 = [...this.카카오들오리지널];
    },
    priceSortB(){
      this.카카오들.sort(function(a,b){
        return a.price - b.price
      });
    },
    priceSortC(){
      this.카카오들.sort(function(a,b){
        return b.price - a.price
      });
    },
    priceSort(){
    if(this.selected == 1){
     this.카카오들 = [...this.카카오들오리지널];
    } else if(this.selected == 2){
      this.카카오들.sort(function(a,b){
        return a.price - b.price
      });
    } else if(this.selected == 3){
      this.카카오들.sort(function(a,b){
        return b.price - a.price
      });
    }

    },
      onActive() {
          if (this.flag==false) {
            this.flag = true
            return this.isActive = true 
          }
          else {
            this.flag = false
            return this.isActive = false
          }
      },
  },
  components: {
    Card
  }
}
</script>

